import Vue from 'vue'
import App from './App.vue'
import store from './store'
import router from './router'

//Js S
import commonUtil from './commonjs/common.js';
Vue.prototype.commonUtil = commonUtil //axios->Vue
//Js E

// Start

import axios from 'axios'
import ElementUI from 'element-ui';
import 'element-ui/lib/theme-chalk/index.css';

Vue.use(ElementUI);
Vue.prototype.$http = axios //axios->Vue

// token
axios.interceptors.request.use(
  config => {
    if (localStorage.getItem('Authorization')) {
      config.headers.Authorization = localStorage.getItem('Authorization');
    }
 
    return config;
  },
  error => {
    return Promise.reject(error)
  });

  //axios.defaults.baseURL = 'http://www.rlf99.com';
  //axios.defaults.baseURL = 'http://localhost:49427';
  // axios.defaults.baseURL = process.env.NODE_ENV === 'development'?'http://localhost:49427':'http://www.rlf99.com'

  // 
  axios.interceptors.response.use(function (response) {
    
    //
    return response;

  }, function (error) {

    // 
    if (error.response.status === 401) {
        // const data = {
        //     code:-1,
        //     message:''
        // }
        // return Promise.reject(data);

        //console.log(error);

        alert("!!");

        //,
        localStorage.removeItem('Authorization');      
        router.push('/login');        
    }
    return Promise.reject(error);
  });

// End

Vue.config.productionTip = false

new Vue({
  store,
  router,
  render: h => h(App)
}).$mount('#app')
